:global * {
  box-sizing: border-box;
}
.SearchBar {
  display: flex;
  padding: 10.5px;
}
.wrap {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  justify-content: center;
  position: relative;
}

.input {
  color: rgb(224, 224, 224) !important;
  background: black !important;
  border: 1px solid #393939;
  padding: 0px 10px;
  width: 90%;
}

.input input {
  padding: 4px 8px;
}

.iconButton {
  color: #888 !important;
  background: #393939 !important;
  border-radius: 0 !important;
  /* padding: 9px 10px !important; */
  font-size: unset;
}

.iconButton:hover {
  color: #999 !important;
}
.Suggetions {
  background: white;
  height: 40vh;
  width: 90%;
  position: absolute;
  top: 40px;
  left: 23px;
  overflow-y: scroll;
}
.Suggetions::-webkit-scrollbar {
  width: 0.5em;
}
.Suggetions::-webkit-scrollbar-track {
  background: transparent;
}
.Suggetions::-webkit-scrollbar-thumb {
  background: rgb(102, 102, 102, 0);
  transition: all 0.3s ease;
}
.Suggetions:hover::-webkit-scrollbar-thumb {
  background: rgb(110, 110, 110);
}
